.header .top-bar {
  position: relative;
  z-index: 3;
  padding: .12rem 0;
  color: #fefefe;
  box-shadow: 0 0.03rem 0.05rem #000;
  background: #333 url(../assets/images/style/pattern.png) repeat 0 0;
}

.header .top-bar:before, .header .top-bar:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  right: 0;
}

.header .top-bar:before {
  top: 0;
  height: .04rem;
  background-color: #03A9F4;
  animation: hue 20s infinite linear;
}

.header .top-bar:after {
  bottom: .03rem;
  height: .01rem;
  border-top: 0.01rem dashed #fefefe;
}

.header .top-info {
  font-size: .12rem;
}

.header .top-info::after {
  content: "";
  display: table;
  clear: both;
}

.header .top-info li {
  box-sizing: border-box;
  width: 50%;
  padding: .04rem 0;
  float: left;
  text-align: center;
}

.header .top-info .slogan {
  width: 100%;
  font-size: .2rem;
  font-weight: 700;
  font-family: "Open Sans Condensed", sans-serif;
}

.header .top-info .icon {
  width: .16rem;
  height: .16rem;
  margin-right: .04rem;
}

.header .follow-me::after {
  content: "";
  display: table;
  clear: both;
}

.header .follow-me li {
  box-sizing: border-box;
  float: right;
  padding-left: .1rem;
}

.header .follow-me li a:hover .icon {
  color: #99c43e;
  transition: all 0.3s linear;
}

