  display: inline-block;
  vertical-align: middle;
  width: 2rem;
  height: 2rem;
}

.header .logo img {
  height: 100%;
  width: 100%;
}

.header .nav-toggle {
  position: relative;
  z-index: 6;
  text-align: center;
}

.header .nav-toggle:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 100%;
  right: 0;
  z-index: 0;
  background-color: #99c43e;
  transition: left 600ms ease-out;
}

.header .nav-toggle .text {
  position: relative;
  font-weight: 600;
  color: #fefefe;
  opacity: 0;
  display: inline-block;
  line-height: .34rem;
}

.header .nav-toggle .hamburger {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 3;
  padding: .04rem .08rem;
  background-color: #99c43e;
  overflow: hidden;
}

.header .nav-toggle .hamburger > span {
  display: block;
  text-indent: -999em;
  height: .02rem;
  width: .2rem;
  background-color: #362f2d;
  margin: .05rem 0;
}

.header .nav-toggle.active:before {
  left: 0;
}

.header .nav-toggle.active .text {
  opacity: 1;
  animation: zoomIn 1s ease-in-out;
}

.header .nav-toggle.active .hamburger span {
  transition: all 400ms ease-out;
  background-color: #fff;
}

.header .nav-toggle.active .hamburger span:first-child {
  transform: rotate(45deg) translateX(5px) translateY(5px);
}

.header .nav-toggle.active .hamburger span:nth-child(2) {
  opacity: 0;
}

.header .nav-toggle.active .hamburger span:last-child {
  transform: rotate(-45deg) translateX(5px) translateY(-5px);
}

.header .nav {
  display: none;
  position: absolute;
  top: inherit;
  left: var(--container-padding);
  right: var(--container-padding);
  z-index: 3000;
  padding: .15rem 0;
  background: #362f2d url(../assets/images/style/pattern.png) repeat 0 0;
